<script>
import ContentHeader from "@/index/shared/ContentHeader.vue";
import { dataTableService } from "@/common/datatable-mixin.js";
import ZohoSearch from "@/components/ZohoSearch.vue";
import ReceiptOne from "@/index/shared/ReceiptOne.vue";
import { permissionsHandler } from "@/common/permissions-handler";
export default {
  name: "SalesReport",
  mixins: [dataTableService, permissionsHandler],
  components: { ContentHeader, ZohoSearch, ReceiptOne },
  data: () => ({
    selectedRow: null,
    paymentModes: [],
    colours: [
      "#8e44ad",
      "#f1c40f",
      "#e67e22",
      "#e74c3c",
      "#95a5a6",
      "#f39c12",
      "#d35400",
      "#c0392b",
      "#bdc3c7",
      "#7f8c8d",
    ],
  }),
  computed: {
    isSalesReport() {
      return this.$route.name == "SalesReport";
    },
    payments() {
      return this.rows.reduce((a, b) => [...a, ...b.payments], []);
    },
    paymentPercentages() {
      let result = [];
      this.paymentModes.forEach((mode) => {
        const payments = this.payments.filter(
          (payment) => payment.payment_type == mode.payment_type
        );
        result.push({
          mode: mode.name,
          amount: payments.reduce((a, b) => a + b.amount_paid, 0),
        });
      });
      return result;
    },
    totalPaidAmount() {
      return this.rows.reduce((a, b) => a + b.paid_amount, 0);
    },
    isFrontOfficeRequest() {
      return this.$route.name == "FrontOfficeSalesReport";
    },
  },
  created() {
    this.fetchUrl =
      this.isSalesReport || this.isFrontOfficeRequest
        ? `reports/sales?is-cashier=${this.isFrontOfficeRequest}`
        : `reports/sales?category=credited`;
    this.handlePaginationSearch();
  },
  methods: {
    handleDestroy(orderId) {
      this.$http.get(`orders/delete/${orderId}`).then((response) => {
        if (response.data.status) {
          let index = this.rows.findIndex((item) => item.id == orderId);
          this.rows.splice(index, 1);
          this.$store.commit("SET_FLASH_MESSAGE", {
            type: "success",
            message: "Sales deleted successfully",
          });
        }
      });
    },
    selectRow(row) {
      this.selectedRow = row;
      this.toggleModal();
    },
  },
  mounted() {
    this.$http.get("shared/used-payment-modes").then((response) => {
      this.paymentModes = response.data.rows;
    });
  },
};
</script>
<template>
  <content-header
    :title="`${isSalesReport ? 'Sales' : 'Credit Post'} Report`"
    subtitle="Explore report and it details throughout period of time"
    v-if="!isFrontOfficeRequest"
  ></content-header>
  <div class="content__boxed">
    <div class="content__wrap">
      <div class="card">
        <div class="card-header">
          <div class="row">
            <div class="col-md-7 d-flex align-items-center mb-1">
              <div
                class="date-range d-flex align-items-center"
                style="width: 200px"
              >
                <div class="d-flex align-items-center input-wrapper">
                  <input
                    type="date"
                    v-model="newFilter.from"
                    class="form-control"
                    @click="displayPicker"
                  />
                  <span class="input-separator">-</span>
                  <input
                    type="date"
                    v-model="newFilter.to"
                    class="form-control"
                    @click="displayPicker"
                  />
                </div>
                <div class="icon ms-auto">
                  <i class="demo-pli-calendar-4 fs-5"></i>
                </div>
              </div>
              <div class="ms-1">
                <zoho-search
                  url="clients/search"
                  label="name"
                  primary-key="id"
                  placeholder="select a client"
                  v-model="newFilter.client"
                  :load-defaults="true"
                  :records="
                    isSalesReport
                      ? [{ id: 'walk-in', name: 'Walk-in Customer' }]
                      : []
                  "
                  fetch-flag="SEARCHING_FOR_CLIENTS"
                ></zoho-search>
              </div>
              <div class="ms-1">
                <zoho-search
                  url="users/search?role=waiters"
                  label="name"
                  primary-key="id"
                  placeholder="select a waiter"
                  v-model="newFilter.waiter"
                  :load-defaults="true"
                  fetch-flag="SEARCHING_FOR_WAITERS"
                ></zoho-search>
              </div>
            </div>
            <div
              class="col-md-5 d-flex gap-1 align-items-center justify-content-md-end mb-1"
            >
              <button class="btn btn-primary" @click="handlePaginationFilter">
                <span>Apply</span>
              </button>
              <button
                class="btn btn-icon btn-outline-primary"
                @click="resetFilters"
              >
                <i class="demo-pli-refresh fs-5"></i>
              </button>
              <button class="btn btn-icon btn-outline-light">
                <i class="demo-pli-printer fs-5"></i>
              </button>
              <button class="btn btn-icon btn-outline-light">
                <i class="demo-pli-file-excel fs-5"></i>
              </button>
            </div>
          </div>
        </div>
        <div class="card-body">
          <div
            class="w-100 p-3 mb-4 rounded border shadow"
            v-if="isSalesReport"
          >
            <div class="d-flex align-items-center">
              <svg
                height="42px"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 24 24"
                fill="currentColor"
              >
                <path
                  d="m23.59 3.47a5.1 5.1 0 0 0 -3.05-3.05c-1.31-.42-2.5-.42-4.92-.42h-7.26c-2.4 0-3.61 0-4.9.4a5.1 5.1 0 0 0 -3.05 3.06c-.41 1.3-.41 2.5-.41 4.9v7.27c0 2.41 0 3.6.4 4.9a5.1 5.1 0 0 0 3.05 3.05c1.3.41 2.5.41 4.9.41h7.28c2.41 0 3.61 0 4.9-.4a5.1 5.1 0 0 0 3.06-3.06c.41-1.3.41-2.5.41-4.9v-7.25c0-2.41 0-3.61-.41-4.91zm-6.17 4.63-.93.93a.5.5 0 0 1 -.67.01 5 5 0 0 0 -3.22-1.18c-.97 0-1.94.32-1.94 1.21 0 .9 1.04 1.2 2.24 1.65 2.1.7 3.84 1.58 3.84 3.64 0 2.24-1.74 3.78-4.58 3.95l-.26 1.2a.49.49 0 0 1 -.48.39h-1.79l-.09-.01a.5.5 0 0 1 -.38-.59l.28-1.27a6.54 6.54 0 0 1 -2.88-1.57v-.01a.48.48 0 0 1 0-.68l1-.97a.49.49 0 0 1 .67 0c.91.86 2.13 1.34 3.39 1.32 1.3 0 2.17-.55 2.17-1.42s-.88-1.1-2.54-1.72c-1.76-.63-3.43-1.52-3.43-3.6 0-2.42 2.01-3.6 4.39-3.71l.25-1.23a.48.48 0 0 1 .48-.38h1.78l.1.01c.26.06.43.31.37.57l-.27 1.37c.9.3 1.75.77 2.48 1.39l.02.02c.19.2.19.5 0 .68z"
                ></path>
              </svg>
              <div class="ms-2">
                <h3 class="mb-0">{{ formatMoney(totalPaidAmount) }}</h3>
                <h5 class="fw-normal">Total Paid Amount</h5>
              </div>
            </div>
            <div class="d-flex align-items-center justify-content-between pt-3">
              <div
                class="d-flex align-items-center"
                v-for="(item, i) in paymentPercentages"
                :key="i"
              >
                <div
                  class="dot"
                  :style="`width: 18px; height: 18px; background-color:${colours[i]}`"
                ></div>
                <h4 class="mb-0 ms-1">
                  {{ formatMoney(item.amount) }}
                  <small>{{ item.mode }}</small>
                </h4>
              </div>
            </div>
          </div>
          <div class="table-responsive">
            <table class="table table-striped table-sm">
              <thead>
                <tr>
                  <th>Receipt #</th>
                  <th v-if="isSalesReport">Table</th>
                  <th>Waiter</th>
                  <th v-if="!isFrontOfficeRequest">Branch</th>
                  <th>Client</th>
                  <th>Amount</th>
                  <th>Paid</th>
                  <th>Balance</th>
                  <th v-if="isSalesReport && !isFrontOfficeRequest">Cashier</th>
                  <th>Status</th>
                  <th>Created at</th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="order in rows" :key="order.reference">
                  <td>
                    <span>#{{ $helper.generateVoucherNo(order.id) }}</span>
                  </td>
                  <td v-if="isSalesReport">
                    <a class="btn-link" href="javascript:void(0)">{{
                      order.table.name
                    }}</a>
                  </td>
                  <td>
                    <span class="fw-bolder">
                      {{ order.waiter ? order.waiter.name : "Admin" }}</span
                    >
                  </td>
                  <td v-if="!isFrontOfficeRequest">
                    <a class="btn-link" href="javascript:void(0)">
                      {{ order.branch ? order.branch.name : "Headquarter" }}</a
                    >
                  </td>
                  <td>
                    <span>{{
                      order.client ? order.client.name : "Walk-in"
                    }}</span>
                  </td>
                  <td>
                    <span class="fw-bolder">{{
                      formatMoney(order.grand_total)
                    }}</span>
                  </td>
                  <td>
                    <span class="fw-bolder">{{
                      formatMoney(order.paid_amount)
                    }}</span>
                  </td>
                  <td>
                    <span class="fw-bolder">{{
                      formatMoney(order.grand_total - order.paid_amount)
                    }}</span>
                  </td>
                  <td v-if="isSalesReport && !isFrontOfficeRequest">
                    <span class="fw-bolder">
                      {{ order.cashier ? order.cashier.name : "Admin" }}</span
                    >
                  </td>
                  <td>
                    <span class="badge bg-success" v-if="order.paid == 1"
                      >Paid</span
                    >
                    <span class="badge bg-warning" v-else>Due</span>
                  </td>
                  <td>
                    <span>{{
                      `${$helper.formatDate(
                        order.created_at
                      )} ${$helper.formatOrderTime(order.created_at)}`
                    }}</span>
                  </td>
                  <td class="text-end text-nowrap">
                    <div class="">
                      <button
                        class="btn btn-icon btn-xs btn-light"
                        data-bs-toggle="dropdown"
                        aria-expanded="false"
                      >
                        <i class="demo-pli-dot-horizontal"></i>
                        <span class="visually-hidden">Toggle Dropdown</span>
                      </button>
                      <ul class="dropdown-menu dropdown-menu-end" style="">
                        <li v-if="hasPermissionTo('R')">
                          <a
                            class="dropdown-item"
                            href="javascript:void(0)"
                            @click.prevent="selectRow(order)"
                            ><svg
                              height="16px"
                              xmlns="http://www.w3.org/2000/svg"
                              viewBox="0 0 24 24"
                              fill="none"
                              stroke="currentColor"
                              stroke-width="2"
                              stroke-linecap="round"
                              stroke-linejoin="round"
                              class="me-1"
                            >
                              <path
                                d="M1 12s4-8 11-8 11 8 11 8-4 8-11 8-11-8-11-8z"
                              ></path>
                              <circle cx="12" cy="12" r="3"></circle></svg
                            >View Items</a
                          >
                        </li>
                        <li v-if="hasPermissionTo('D')">
                          <a
                            class="dropdown-item"
                            href="javascript:void(0)"
                            @click.prevent="
                              $confirm({
                                title: 'Delete sale record',
                                content: `Are you sure you want to delete this sale #${$helper.generateVoucherNo(
                                  order.id
                                )} ?`,
                                classes: 'btn btn-danger',
                                actionText: 'Yes, Continue',
                                closeText: 'Cancel',
                                action: () => {
                                  handleDestroy(order.id);
                                },
                              })
                            "
                            ><i class="demo-pli-trash fs-5 me-1"></i>Delete</a
                          >
                        </li>
                      </ul>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <pagination
            :from="recordsFrom"
            :to="recordsTo"
            :current_page="currentPage"
            :row_count_page="rowCountPage"
            @page-update="handlePagination"
            :total_records="totalRows"
            :page_range="pageRange"
          ></pagination>
        </div>
      </div>
    </div>
  </div>
  <div
    class="receipt receipt-end border-0 shadow"
    tabindex="-1"
    style="visibility: visible"
    aria-modal="true"
    role="dialog"
    :class="{ 'show-receipt': selectedRow != null }"
  >
    <div class="receipt-header bg-dark">
      <h4 class="receipt-title">RECEIPT</h4>
      <div class="text-center text-nowrap">
        <a
          class="btn btn-icon btn-xs btn-success mx-2"
          href="javascript:void(0)"
          @click.prevent="$helper.handlePrint('#print-container')"
        >
          <svg
            height="18px"
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M17 17h2a2 2 0 002-2v-4a2 2 0 00-2-2H5a2 2 0 00-2 2v4a2 2 0 002 2h2m2 4h6a2 2 0 002-2v-4a2 2 0 00-2-2H9a2 2 0 00-2 2v4a2 2 0 002 2zm8-12V5a2 2 0 00-2-2H9a2 2 0 00-2 2v4h10z"
            ></path></svg
        ></a>
        <a
          class="btn btn-icon btn-xs btn-danger"
          href="javascript:void(0)"
          @click.prevent="selectRow(null)"
          ><i class="demo-pli-cross fs-5 fw-bolder"></i
        ></a>
      </div>
    </div>
    <div class="receipt-body scrollable-content px-1">
      <receipt-one
        v-if="selectedRow != null"
        :order="selectedRow"
      ></receipt-one>
    </div>
  </div>
</template>
