<template>
  <content-header title="Dashboard" subtitle="Welcome back to the Dashboard!">
    <template #description>
      <p>Scroll down to see quick links and overviews</p>
    </template>
  </content-header>
  <div class="content__boxed">
    <div class="content__wrap">
      <div class="nk-content py-4">
        <div class="container-fluid">
          <div class="nk-content-body">
            <div class="nk-block">
              <div class="row">
                <div class="col-xl-12">
                  <orders-chart :ordersChart="orders_chart"></orders-chart>
                </div>
                <div class="col-xl-6 py-2">
                  <categories-chart
                    :categories-chart="categoriesChart"
                    :total-orders="totalOrders"
                  ></categories-chart>
                </div>
                <div class="col-lg-6 py-2">
                  <div class="card h-100">
                    <div class="card-body">
                      <h4>Top Selling items</h4>

                      <div class="row">
                        <div
                          class="col-lg-6"
                          v-for="(item, i) in topSelling"
                          :key="i"
                        >
                          <div class="d-flex align-items-center w-100">
                            <div class="" style="width: 50% !important">
                              <apexchart
                                type="radialBar"
                                :options="getSellingPercentage(item)"
                                :series="[item]"
                              ></apexchart>
                            </div>
                            <div class="w-100">
                              <div class="d-flex align-items-center">
                                <div class="flex-grow-1 ms-2">
                                  <h4 class="mb-1 font-size-17">
                                    {{ $helper.formatNumber(item) }}
                                    <small>Items</small>
                                  </h4>
                                </div>

                                <div class="flex-shrink-0 text-end">
                                  <span
                                    class="badge rounded-pill badge-soft-success font-size-12 fw-medium mb-1"
                                    ><i class="mdi mdi-arrow-up-bold me-2"></i
                                    >{{
                                      $helper.formatNumber(
                                        (item / soldItems) * 100
                                      )
                                    }}%</span
                                  >
                                </div>
                              </div>
                              <h6>{{ i }}</h6>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="col-lg-3">
                  <div class="card">
                    <div class="card-body">
                      <h4 class="card-title mb-1">Orders Group</h4>
                      <div class="my-0">
                        <p class="mb-2 h5 fw-normal">
                          Dine In
                          <span class="float-end"
                            >{{
                              $helper.formatNumber(
                                orderFromPercentarge.dine_in
                              )
                            }}%</span
                          >
                        </p>
                        <div class="progress" style="height: 6px">
                          <div
                            class="progress-bar progress-bar-striped bg-seconday"
                            role="progressbar"
                            :style="`width: ${orderFromPercentarge.dine_in}%`"
                            :aria-valuenow="orderFromPercentarge.dine_in"
                            aria-valuemin="0"
                            :aria-valuemax="orderFromPercentarge.dine_in"
                          ></div>
                        </div>

                        <p class="my-2 h5 fw-normal">
                          Take Away
                          <span class="float-end"
                            >{{
                              $helper.formatNumber(
                                orderFromPercentarge.take_away
                              )
                            }}%</span
                          >
                        </p>
                        <div class="progress" style="height: 6px">
                          <div
                            class="progress-bar progress-bar-striped bg-success"
                            role="progressbar"
                            :style="`width: ${orderFromPercentarge.take_away}%`"
                            :aria-valuenow="orderFromPercentarge.take_away"
                            aria-valuemin="0"
                            :aria-valuemax="orderFromPercentarge.take_away"
                          ></div>
                        </div>

                        <p class="mt-3 mb-2 h5 fw-normal">
                          Delivery
                          <span class="float-end"
                            >{{
                              $helper.formatNumber(
                                orderFromPercentarge.delivery
                              )
                            }}%</span
                          >
                        </p>
                        <div class="progress mt-2" style="height: 6px">
                          <div
                            class="progress-bar progress-bar-striped bg-danger"
                            role="progressbar"
                            :style="`width: ${orderFromPercentarge.delivery}%`"
                            :aria-valuenow="orderFromPercentarge.delivery"
                            aria-valuemin="0"
                            :aria-valuemax="orderFromPercentarge.delivery"
                          ></div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="col-lg-3">
                  <div class="card">
                    <div class="card-body">
                      <div class="row g-0">
                        <div class="col-sm-6">
                          <div class="border-bottom border-end p-3 h-100">
                            <p class="text-muted text-truncate mb-1">
                              Total Orders
                            </p>
                            <h5 class="text-truncate mb-0">
                              {{ $helper.formatNumber(totalOrders) }}
                            </h5>
                          </div>
                        </div>
                        <div class="col-sm-6">
                          <div class="border-bottom p-3 h-100">
                            <p class="text-muted text-truncate mb-1">
                              Sales Amount
                            </p>
                            <h5 class="text-truncate mb-0">
                              {{ formatMoney(salesAmount) }}
                            </h5>
                          </div>
                        </div>
                      </div>
                      <div class="row g-0">
                        <div class="col-sm-6">
                          <div class="border-end p-3 h-100">
                            <p class="text-muted text-truncate mb-1">
                              Items Sold
                            </p>
                            <h5 class="text-truncate mb-0">
                              {{ $helper.formatNumber(soldItems) }}
                            </h5>
                          </div>
                        </div>
                        <div class="col-sm-6">
                          <div class="p-3 h-100">
                            <p class="text-muted text-truncate mb-1">
                              Menu Items
                            </p>
                            <h5 class="text-truncate mb-0">
                              {{ $helper.formatNumber(menuItems) }}
                            </h5>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import "@/assets/dashboard.css";
import ContentHeader from "@/index/shared/ContentHeader.vue";
import OrdersChart from "./OrdersChart.vue";
import CategoriesChart from "./CategoriesChart.vue";
import VueApexCharts from "vue3-apexcharts";
import { colours } from "@/common/utils";
export default {
  name: "Index",
  components: {
    ContentHeader,
    OrdersChart,
    CategoriesChart,
    apexchart: VueApexCharts,
  },
  data: () => ({
    orders_chart: {},
    totalOrders: 0,
    salesAmount: 0,
    soldItems: 0,
    menuItems: 0,
    categoriesChart: {},
    topSelling: {},
    orderFrom: {},
  }),
  computed: {
    orderFromPercentarge() {
      return {
        dine_in: (this.orderFrom.dine_in / this.totalOrders) * 100,
        take_away: (this.orderFrom.take_away / this.totalOrders) * 100,
        delivery: (this.orderFrom.delivery / this.totalOrders) * 100,
      };
    },
  },
  beforeCreate() {
    this.$http.get("dashboards").then((response) => {
      this.orders_chart = response.data.orders_chart;
      this.totalOrders = response.data.total_orders;
      this.salesAmount = response.data.sales_amount;
      this.soldItems = response.data.sold_items;
      this.categoriesChart = response.data.categories_chart;
      this.menuItems = response.data.menu_items;
      this.topSelling = response.data.top_selling;
      this.orderFrom = response.data.order_from;
    });
  },
  methods: {
    getSellingPercentage(count) {
      return {
        fill: { colors: colours[Math.floor(Math.random() * colours.length)] },
        series: [70],
        chart: {
          type: "radialBar",
          width: 50,
          height: 50,
          sparkline: { enabled: !0 },
        },
        dataLabels: { enabled: !1 },
        plotOptions: {
          radialBar: {
            hollow: { margin: 0, size: `${(count / this.soldItems) * 100}%` },
            track: { margin: 0 },
            dataLabels: { show: !1 },
          },
        },
      };
    },
  },
};
</script>
