<script>
import { dataTableService } from "@/common/datatable-mixin.js";
import ZohoSearch from "@/components/ZohoSearch.vue";
import ContentHeader from "@/index/shared/ContentHeader.vue";
import PrintHeader from "@/components/PrintHeader.vue";
import ReceiptOne from "@/index/shared/ReceiptOne.vue";
export default {
  name: "PaymentsReceived",
  components: { ZohoSearch, ContentHeader, PrintHeader, ReceiptOne },
  mixins: [dataTableService],
  data: () => ({
    excelFilename: "Payments-Received",
  }),
  computed: {
    grandTotal() {
      return this.rows.reduce((a, b) => a + Number(b.amount_paid), 0);
    },
    paymentModes() {
      return this.$store.state.paymentModes;
    },
  },
  created() {
    //this.permissionsPath = "production.submenus.invoices";
    this.fetchUrl = `reports/sales/payments-received`;
    const startDate = this.$route?.query?.start_date;
    const endDate = this.$route?.query?.end_date;
    if (startDate && endDate) {
      this.newFilter.from = startDate;
      this.newFilter.to = endDate;
      this.handlePaginationFilter(
        this.encodeQuery(this.fetchUrl, this.newFilter)
      );
    } else {
      this.handlePaginationFilter();
    }
  },
  methods: {
    handleDestroy(orderId) {
      this.$http.get(`orders/delete/${orderId}`).then((response) => {
        if (response.data.status) {
          let index = this.rows.findIndex((item) => item.id == orderId);
          this.rows.splice(index, 1);
          this.$store.commit("SET_FLASH_MESSAGE", {
            type: "success",
            message: "Sales deleted successfully",
          });
        }
      });
    },
    selectRow(row) {
      this.selectedRow = row;
      this.toggleModal();
    },
  },
  mounted() {
    this.getPaymentsMeta();
  },
};
</script>
