<script>
import VueApexCharts from "vue3-apexcharts";
export default {
  name: "OrdersChart",
  components: {
    apexchart: VueApexCharts,
  },
  props: {
    ordersChart: {
      type: Object,
      default: function () {
        return {};
      },
    },
  },
  computed: {
    options() {
      return {
        chart: {
          height: 300,
          type: "bar",
        },
        dataLabels: {
          enabled: false,
        },
        stroke: {
          curve: "smooth",
        },
        colors: ["#00ADA3", "#FFAB2D"],
        yaxis: {
          labels: {
            style: {
              colors: "#B5B5C3",
              fontSize: "12px",
              fontWeight: 400,
            },
            formatter: (value) => {
              var result = value;
              if (value >= 1000000000 || value <= -1000000000) {
                result = value / 1e9 + "B";
              } else if (value >= 1000000 || value <= -1000000) {
                result = value / 1e6 + "M";
              } else if (value >= 1000 || value <= -1000) {
                result = value / 1e3 + "K";
              }
              return `${this.appSettings.currency} ${result}`;
            },
          },
        },
        xaxis: {
          type: "number",
          categories: this.ordersChart.labels,
        },
      };
    },
    series() {
      return [
        {
          name: "Orders",
          data: this.ordersChart.data,
        },
      ];
    },
  },
};
</script>
<template>
  <div class="card">
    <div class="card-body">
      <div class="card-head"><h4 class="title">Sales Chart</h4></div>
      <div class="nk-cks">
        <apexchart
          type="bar"
          height="300"
          :options="options"
          :series="series"
        ></apexchart>
      </div>
    </div>
  </div>
</template>
