<template>
  <content-header
    title="Invoices Bulk Payment"
    subtitle="Pay for related invoices."
  ></content-header>
  <div class="content__boxed">
    <div class="content__wrap">
      <div class="card">
        <div class="card-body">
          <div class="table-responsive">
            <table
              class="table align-middle table-nowrap table-striped table-sm"
              v-if="items.length"
            >
              <thead>
                <tr>
                  <th><span>$/N</span></th>
                  <th><span>Due Date</span></th>
                  <th>
                    <span>Invoice #</span>
                  </th>
                  <th>
                    <span>Payer</span>
                  </th>
                  <th>
                    <span>Total</span>
                  </th>
                  <th>
                    <span>Paid</span>
                  </th>
                  <th>
                    <span>Balance</span>
                  </th>
                  <th>Amount To Pay</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(row, i) in items" :key="'row' + i">
                  <td>
                    <span>{{ i + 1 }}</span>
                  </td>
                  <td>
                    <span class="title">{{
                      $helper.formatDate(row.committed_date)
                    }}</span>
                  </td>
                  <td>#{{ $helper.padNumber(row.id) }}</td>
                  <td>
                    <span>{{ row?.client || row?.waiter }}</span>
                  </td>
                  <td>
                    <span> {{ formatMoney(row.total) }}</span>
                  </td>
                  <td>
                    <span> {{ formatMoney(row.amount_paid) }}</span>
                  </td>
                  <td>
                    <span> {{ formatMoney(row.balance) }}</span>
                  </td>
                  <td>
                    <div class="form-control-wrap">
                      <div class="input-group">
                        <div class="input-group-text">RWF</div>
                        <input
                          type="number"
                          class="form-control"
                          placeholder="Payment Amount"
                          v-model="items[i].amount"
                        />
                      </div>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td></td>
                  <td><h6>Grand Total</h6></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td>
                    <h5>{{ formatMoney(totalBalance) }}</h5>
                  </td>
                  <td>
                    <h5>{{ formatMoney(grandTotal) }}</h5>
                  </td>
                </tr>
              </tbody>
            </table>
            <div class="row justify-content-end">
              <div class="col-xl-5 col-lg-6 col-md-8">
                <div class="row align-items-center mb-2">
                  <label class="form-label col-sm-4">Issued date</label>
                  <div class="col-sm-8">
                    <input
                      type="date"
                      v-model="committed_date"
                      class="form-control"
                      placeholder="Issued date"
                    />
                  </div>
                </div>
                <div class="row align-items-center mb-2">
                  <label class="form-label col-sm-4">Payment Mode</label>
                  <div class="col-sm-8">
                    <select
                      class="form-control form-select"
                      v-model="payment_type"
                    >
                      <option value="null" selected hidden>
                        Payment Method
                      </option>
                      <option
                        v-for="row in paymentModes"
                        :key="row.id"
                        :value="row.id"
                      >
                        {{ row.name }}
                      </option>
                    </select>
                  </div>
                </div>
                <div class="row align-items-center mb-2">
                  <label class="form-label col-sm-4">Deposited To:</label>
                  <div class="col-sm-8">
                    <select
                      class="form-control form-select"
                      v-model="account_id"
                    >
                      <option value="null" selected hidden>Deposited To</option>
                      <option
                        v-for="row in accounts"
                        :key="row.id"
                        :value="row.id"
                      >
                        {{ row.name }}
                      </option>
                    </select>
                  </div>
                </div>
                <div class="row align-items-center mb-2">
                  <label class="form-label col-sm-4">Reference</label>
                  <div class="col-sm-8">
                    <input
                      type="text"
                      class="form-control"
                      v-model="payment_ref"
                      placeholder="Payment REF"
                    />
                  </div>
                </div>
                <div class="row align-items-center mb-2">
                  <div class="col-sm-4"></div>
                  <div class="col-sm-8">
                    <wolf-button
                      type="button"
                      @clicked="handleSubmit"
                      :disabler="!items.length || !payment_type || !account_id"
                      activator="SENDING_REQUEST"
                      class="btn btn-primary"
                      >Save</wolf-button
                    >
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import ContentHeader from "@/index/shared/ContentHeader.vue";
export default {
  name: "InvoicesBulkPayment",
  components: { ContentHeader },
  data: () => ({
    vouchers: [],
    items: [],
    payment_type: null,
    account_id: null,
    payment_ref: null,
    committed_date: new Date(TODAY)
      .toLocaleString("fr-CA", { timeZone: TIMEZONE })
      .slice(0, 10),
  }),
  computed: {
    grandTotal() {
      return this.items.reduce((a, b) => a + Number(b.amount), 0).toFixed(3);
    },
    totalBalance() {
      return this.items.reduce((a, b) => a + Number(b.balance), 0).toFixed(3);
    },
    accounts() {
      return this.$store.state.accounts;
    },
    paymentModes() {
      return this.$store.state.paymentModes;
    },
  },
  created() {
    const vounchers = this.$route.query.vouchers;
    this.$http.get(`payments/vounchers/${vounchers}`).then((response) => {
      this.vouchers = response.data.vouchers;
      this.vouchers.forEach((item) => {
        this.items.push({
          id: item.id,
          code: item.reference,
          client: item?.client?.name || item?.waiter?.name,
          committed_date: item.created_at,
          amount: item.amount_remain,
          balance: item.amount_remain,
          total: item.grand_total,
          amount_paid: item.amount_paid,
        });
      });
    });
  },
  methods: {
    handleSubmit() {
      this.$http
        .post(
          "payments/bulk",
          this.$helper.generateFormData({
            items: this.items,
            payment_type: this.payment_type,
            account_id: this.account_id,
            committed_date: this.committed_date,
            payment_ref: this.payment_ref,
          })
        )
        .then((response) => {
          if (response.data.status) {
            this.$store.commit("SET_FLASH_MESSAGE", {
              type: "success",
              message: response.data.message,
            });
            this.$router.replace({ name: "CustomerBalance" });
          }
        })
    },
  },
  mounted() {
    this.getPaymentsMeta();
  },
};
</script>
