<template>
  <content-header
    title="Pending Payments Report"
    subtitle="Pending invoices per customer"
  ></content-header>
  <div class="content__boxed">
    <div class="content__wrap">
      <div class="card">
        <div class="card-header">
          <div
            class="w-100 justify-content-between d-flex align-items-center my-3"
          >
            <div class="left-toobar d-flex flex-nowrap">
              <div
                class="date-range d-flex align-items-center"
                style="width: 200px"
              >
                <div class="d-flex align-items-center input-wrapper">
                  <input
                    type="date"
                    v-model="newFilter.from"
                    class="form-control"
                    @click="displayPicker"
                  />
                  <span class="input-separator">-</span>
                  <input
                    type="date"
                    v-model="newFilter.to"
                    class="form-control"
                    @click="displayPicker"
                  />
                </div>
                <div class="icon ms-auto">
                  <i class="uil uil-calendar-alt fs-5"></i>
                </div>
              </div>
              <div class="ms-2" style="width: 200px">
                <ZohoSearch
                  placeholder="Select client"
                  url="clients/search"
                  label="name"
                  :load-defaults="true"
                  style="z-index: 999"
                  v-model="newFilter.client"
                ></ZohoSearch>
              </div>
              <button
                class="btn-primary btn btn-icon ms-1"
                type="button"
                :disabled="!rows.length"
                @click="handleExcelExport(null)"
              >
                <svg
                  height="18px"
                  viewBox="0 0 16 16"
                  xmlns="http://www.w3.org/2000/svg"
                  fill="currentColor"
                >
                  <path
                    fill-rule="evenodd"
                    d="M2 2a2 2 0 0 1 2-2h5.293A1 1 0 0 1 10 .293L13.707 4a1 1 0 0 1 .293.707V14a2 2 0 0 1-2 2H4a2 2 0 0 1-2-2V2zm7.5 1.5v-2l3 3h-2a1 1 0 0 1-1-1zM5.884 6.68a.5.5 0 1 0-.768.64L7.349 10l-2.233 2.68a.5.5 0 0 0 .768.64L8 10.781l2.116 2.54a.5.5 0 0 0 .768-.641L8.651 10l2.233-2.68a.5.5 0 0 0-.768-.64L8 9.219l-2.116-2.54z"
                  ></path>
                </svg>
              </button>
            </div>
            <div class="right-toobar d-flex align-items-center flex-nowrap">
              <button
                class="btn btn-primary ms-2"
                type="button"
                @click="handlePaginationFilter"
              >
                Apply
              </button>
              <button
                class="btn btn-outline-primary ms-2"
                @click="resetFilters"
                type="button"
              >
                Reset
              </button>
            </div>
          </div>
        </div>
        <div class="card-body">
          <div class="table-responsive">
            <table class="table table-striped table-sm">
              <thead>
                <tr>
                  <th><span>$/N</span></th>
                  <th>
                    <span>Payer</span>
                  </th>
                  <th>
                    <span>Discount</span>
                  </th>
                  <th>
                    <span>Balance</span>
                  </th>
                  <th>Status</th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                <template v-if="rows.length">
                  <tr v-for="(row, i) in rows" :key="'row' + i">
                    <td>
                      <span>{{ i + 1 }}</span>
                    </td>
                    <td>
                      <span v-if="row.client"
                        >{{ row?.client?.name }}(Customer)</span
                      >
                      <span v-else>{{ row?.waiter?.name }}(Waiter)</span>
                    </td>
                    <td>
                      <span
                        >{{
                          row.client
                            ? $helper.formatNumber(row?.client?.discount)
                            : "0"
                        }}%</span
                      >
                    </td>
                    <td>
                      <span class=""> {{ formatMoney(row.balance) }}</span>
                    </td>
                    <td>
                      <span class="badge badge-dot bg-warning">Due</span>
                    </td>
                    <td>
                      <router-link
                        :to="{
                          name: 'InvoicesBulkPayment',
                          query: { vouchers: row.vouchers },
                        }"
                        class="btn btn-primary d-md-inline-flex btn-sm"
                      >
                        <em class="uil uil-coins"></em
                        ><span class="ms-1">Pay Now</span>
                      </router-link>
                    </td>
                  </tr>
                  <tr>
                    <td><span></span></td>
                    <td><h5>Grand Total</h5></td>
                    <td></td>
                    <td>
                      <h6>
                        {{ `${formatMoney(totalBalance)}` }}
                      </h6>
                    </td>
                    <td></td>
                    <td></td>
                  </tr>
                </template>
              </tbody>
            </table>
            <pagination
              :from="recordsFrom"
              :to="recordsTo"
              :current_page="currentPage"
              :row_count_page="rowCountPage"
              @page-update="handlePagination"
              :total_records="totalRows"
              :page_range="pageRange"
            ></pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { dataTableService } from "@/common/datatable-mixin.js";
import ZohoSearch from "@/components/ZohoSearch.vue";
import ContentHeader from "@/index/shared/ContentHeader.vue";
export default {
  name: "CustomerBalance",
  components: { ZohoSearch, ContentHeader },
  mixins: [dataTableService],
  data: () => ({
    items: [],
    record: {},
    client: null,
    rowIndex: null,
    payableAmount: 0,
  }),
  computed: {
    totalBalance() {
      return this.rows.reduce((a, b) => a + Number(b.balance), 0);
    },
  },
  created() {
    this.fetchUrl = `clients/balances`;
    const startDate = this.$route?.query?.start_date;
    const endDate = this.$route?.query?.end_date;
    if (startDate && endDate) {
      this.newFilter.from = startDate;
      this.newFilter.to = endDate;
      this.handlePaginationFilter(
        this.encodeQuery(this.fetchUrl, this.newFilter)
      );
    } else {
      this.handlePaginationFilter();
    }
  },
  methods: {
    handleClientFilter(client) {
      if (!this.$helper.empty(client)) {
        this.newFilter.client = client.id;
        this.handlePaginationFilter();
      }
    },
  },
};
</script>
