<script>
import VueApexCharts from "vue3-apexcharts";
export default {
  name: "CategoriesChart",
  components: {
    apexchart: VueApexCharts,
  },
  props: {
    categoriesChart: {
      type: Object,
      default: function () {
        return {};
      },
    },
    totalOrders: {
      type: Number,
      default: 0,
    },
  },
  computed: {
    chatSeries() {
      return [...Object.values(this.categoriesChart)].map((x) => Number(x));
    },
    chatOptions() {
      return {
        stroke: {
          colors: ["#fff"],
        },
        fill: {
          opacity: 0.8,
        },
        labels: Object.keys(this.categoriesChart),
        responsive: [
          {
            breakpoint: 480,
            options: {
              chart: {
                width: 200,
              },
              legend: {
                position: "bottom",
              },
            },
          },
        ],
      };
    },
  },

  methods: {},
};
</script>
<template>
  <div class="card">
    <div class="card-body">
      <div class="card-head">
        <h4 class="mb-0">Categories Chart</h4>
      </div>
      <div class="nk-ck-sm">
        <apexchart
          type="polarArea"
          :options="chatOptions"
          :series="chatSeries"
        ></apexchart>
        <canvas class="polar-chart" id="categories-chart"></canvas>
      </div>
    </div>
  </div>
</template>
