    generalReports: [
      { name: "Sales Report", route: "SalesReport" },
      { name: "Pending Invoices", route: "CustomerBalance" },
      { name: "Invoices Report", route: "InvoicesReport" },
      { name: "Orders Report", route: "OrdersReport" },
      { name: "Payments Received", route: "PaymentsReceived" },
      { name: "Counter Report", route: "" },
    ],
    leadsReports: [
      { name: "Waiters Report", route: "" },
      { name: "Waiters Balance", route: "WaitersBalanceReport" },
      { name: "Cashiers Report", route: "" },
