    menuReports: [
      { name: "Sales By Item", route: "DetailsReport" },
      { name: "Kitchen Report", route: "" },
      { name: "Beverage Report", route: "" },
      { name: "Profit Report", route: "" },
    ],
  }),
  computed: {
    groups() {
      if (!this.$helper.empty(this.userPermissions)) {
        return this.userPermissions.all_reports.groups || {};
      }
      return {};
    },
  },
  methods: {
    canView(args) {
      const access =
        this.$helper.getKeyValue(args.join("."), this.groups) || "0000";
      return this.userRole == "ADMINISTRATOR" || access[0] == 1;
    },
    isGroupAccessible(group) {
      return (
        this.userRole == "ADMINISTRATOR" ||
        Object.values(this.groups[group]).some((value) => value != "0000")
      );
    },
  },
};
</script>
