<script>
import { dataTableService } from "@/common/datatable-mixin.js";
import { permissionsHandler } from "@/common/permissions-handler";
import EmptyResults from "@/components/EmptyResults.vue";
import OrderModal from "@/components/OrderModal.vue";
import OrderReceipts from "@/components/OrderReceipts.vue";
import ReceiptOne from "@/index/shared/ReceiptOne.vue";
import SplittedReceipt from "@/index/shared/SplittedReceipt.vue";

export default {
  name: "WaiterOrders",
  mixins: [dataTableService, permissionsHandler],
  components: {
    EmptyResults,
    OrderModal,
    ReceiptOne,
    OrderReceipts,
    SplittedReceipt,
  },
  data: () => ({
    selectedOrder: null,
    printedOrder: {},
    splitingMode: false,
    splittedOrder: null,
  }),
  computed: {
    grandTotal() {
      return this.rows.reduce((a, b) => a + b.grand_total, 0);
    },
    paidTotal() {
      let rows = this.rows.filter((row) => row.paid == 1);
      return rows.reduce((a, b) => a + b.grand_total, 0);
    },
  },
  mounted() {
    this.fetchUrl = `orders/show-waiter-orders?paid=0&is-cashier=${this.isCashier}`;
    this.handlePaginationSearch();
    this.getPaymentsMeta();
  },
  methods: {
    handleSplitEvent() {
      this.openSplitModal(this.selectedOrder);
      this.closeSidebar();
    },
    openSplitModal(order) {
      this.splittedOrder = { ...order };
      this.splitingMode = true;
    },
    closeSplitingOrder() {
      this.splittedOrder = null;
      this.splitingMode = false;
    },
    handlePaidOrder(meta) {
      const index = this.rows.findIndex(
        (item) => item.id == this.selectedOrder.id
      );
      if (meta.amount_remain == 0) {
        this.rows.splice(index, 1);
      } else {
        this.rows[index].paid_amount += meta.amount_paid;
        if (meta.credited) {
          this.rows[index].resolved = 1;
        }
      }
      this.closeSidebar();
    },
    removeOrderItem(meta) {
      const index = this.rows.findIndex((item) => item.id == meta.orderId);
      this.rows[index].grand_total -= meta?.item?.amount;
      this.rows[index].amount -= meta?.item?.amount;
      const itemIndex = this.rows[index]?.items.findIndex(
        (item) => item.id == meta.item?.id
      );
      this.rows[index].items.splice(itemIndex, 1);
    },
    closeSidebar() {
      document.querySelector("#root").classList.remove("sb--show");
      this.selectedOrder = null;
    },
    selectOrder(order) {
      if (order) {
        document.querySelector("#root").classList.add("sb--show");
      } else {
        document.querySelector("#root").classList.remove("sb--show");
      }
      this.selectedOrder = order;
    },
    printSplittedOrder(order) {
      order.receipts.forEach((receipt) => {
        this.printedOrder.grand_total = receipt.total;
        this.printedOrder.items = receipt.items;
        setTimeout(() => this.$helper.handlePrint("#print-container"), 3000);
      });
    },
    handleOrderPrint(order, index) {
      this.$confirm({
        title: "Printing order invoice",
        content: `Are you sure you want to print order #${this.$helper.generateVoucherNo(
          order.id
        )} and make it as completed?`,
        classes: "btn btn-primary",
        actionText: "Complete",
        closeText: "Cancel",
        action: () => {
          this.$http
            .get(`orders/invoice/${order.id}/print`)
            .then((response) => {
              if (response.data.status) {
                this.$store.commit("SET_FLASH_MESSAGE", {
                  type: "success",
                  message: "Order sent to print command successfully",
                });
                this.rows[index].status = "DELIVERED";
                this.rows[index].printed = 1;
                //this.rows[index].resolved = 1;
                this.printedOrder = order;
                if (
                  !Array.isArray(order.receipts) &&
                  !order?.receipts?.length
                ) {
                  setTimeout(
                    () => this.$helper.handlePrint("#print-container"),
                    2000
                  );
                }
              }
            });
        },
      });
    },
    handleSplittedOrder(receipts) {
      const index = this.rows.findIndex(
        (item) => item.id == this.splittedOrder.id
      );
      this.rows[index].receipts = receipts;
      this.closeSplitingOrder();
    },
  },
};
</script>
<template>
  <div class="d-none">
    <receipt-one :order="printedOrder"></receipt-one>
  </div>

  <template v-if="printedOrder.receipts">
    <div
      class="d-none"
      v-for="receipt in printedOrder.receipts"
      :key="'receipt' + printedOrder.id"
    >
      <SplittedReceipt
        :order="{
          ...printedOrder,
          ...{ grand_total: receipt.grand_total, items: receipt.items },
        }"
      />
    </div>
  </template>
  <div class="container-fluid">
    <div class="row justify-content-center">
      <div class="pt-1 col-lg-10 col-md-11 col-sm-12">
        <div
          class="d-flex justify-content-center"
          style="min-height: 95vh"
          v-if="!rows.length"
        >
          <empty-results>
            <template #description
              ><span>No orders found. Please place new orders</span></template
            >
          </empty-results>
        </div>
        <div class="card" v-else>
          <div class="card-header">
            <div class="row align-items-center">
              <div class="col-md-6 d-flex gap-1 align-items-center">
                <h4 class="card-title mb-0">
                  {{ isCashier ? "Orders" : "Your Orders" }}
                </h4>
              </div>
              <div
                class="col-md-6 d-flex gap-1 align-items-center justify-content-md-end"
              >
                <div class="form-group" v-if="false">
                  <input
                    type="text"
                    placeholder="Search..."
                    class="form-control"
                    autocomplete="off"
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="card-body pt-1">
            <div class="table-responsive">
              <table class="table table-striped table-sm">
                <thead>
                  <tr>
                    <th>Order #</th>
                    <th>Tot. Amount</th>
                    <th>Paid Amount</th>
                    <th>Items</th>
                    <th>Table</th>
                    <th>Time</th>
                    <th v-if="isCashier">Waiter</th>
                    <th>Status</th>
                    <th></th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(order, i) in rows" :key="order.reference">
                    <td>
                      <a class="btn-link" href="javascript:void(0)">
                        Order #{{ $helper.generateVoucherNo(order.id) }}</a
                      >
                    </td>
                    <td>
                      <span class="fw-bolder">{{
                        formatMoney(order.grand_total)
                      }}</span>
                    </td>
                    <td>
                      <span class="fw-bolder">{{
                        formatMoney(order.paid_amount)
                      }}</span>
                    </td>
                    <td>
                      <span>{{ order.items.length }}</span>
                    </td>
                    <td>
                      <span>{{ order.table.name }}</span>
                    </td>
                    <td>
                      <span>{{
                        $helper.formatOrderTime(order.created_at)
                      }}</span>
                    </td>
                    <td v-if="isCashier">
                      <span>{{ order.waiter.name }}</span>
                    </td>
                    <td class="fs-6">
                      <div
                        class="badge d-block bg-success"
                        v-if="order.status == 'DELIVERED'"
                      >
                        Completed
                      </div>
                      <div
                        class="badge d-block bg-warning"
                        v-else-if="order.status == 'PENDING'"
                      >
                        Pending
                      </div>
                      <div
                        class="badge d-block bg-danger"
                        v-else-if="order.status == 'DENIED'"
                      >
                        Cancelled
                      </div>
                    </td>
                    <td>
                      <div
                        class="d-flex flex-nowrap align-items-center text-success"
                        v-if="order.paid == 1"
                      >
                        <span class="badge rounded bg-success p-1"
                          ><span class="visually-hidden"></span
                        ></span>
                        <span class="ms-1">Paid</span>
                      </div>
                      <div
                        class="d-flex flex-nowrap align-items-center text-warning"
                        v-else
                      >
                        <span class="badge rounded bg-warning p-1"
                          ><span class="visually-hidden"></span
                        ></span>
                        <span class="ms-1">Due</span>
                      </div>
                    </td>
                    <td class="text-center text-nowrap">
                      <a
                        class="btn btn-icon btn-sm btn-info btn-hover"
                        href="javascript:void(0)"
                        @click.prevent="selectOrder(order)"
                      >
                        <svg
                          height="18px"
                          viewBox="0 0 16 16"
                          xmlns="http://www.w3.org/2000/svg"
                          fill="currentColor"
                        >
                          <path
                            fill-rule="evenodd"
                            d="M15 13V4a1 1 0 0 0-1-1h-3.586A2 2 0 0 1 9 2.414l-1-1-1 1A2 2 0 0 1 5.586 3H2a1 1 0 0 0-1 1v9a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1zM2 2a2 2 0 0 0-2 2v9a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V4a2 2 0 0 0-2-2h-3.586a1 1 0 0 1-.707-.293L8.354.354a.5.5 0 0 0-.708 0L6.293 1.707A1 1 0 0 1 5.586 2H2z"
                          ></path>
                          <path
                            fill-rule="evenodd"
                            d="M15 11H1v-1h14v1zm0-4H1V6h14v1zM2 12.5a.5.5 0 0 1 .5-.5h6a.5.5 0 0 1 0 1h-6a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h11a.5.5 0 0 1 0 1h-11a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h8a.5.5 0 0 1 0 1h-8a.5.5 0 0 1-.5-.5z"
                          ></path>
                        </svg>
                      </a>
                      <template v-if="order.printed == 0">
                        <a
                          class="btn btn-icon btn-sm btn-light btn-hover"
                          href="javascript:void(0)"
                          v-if="isCashier"
                          @click.prevent="openSplitModal(order)"
                        >
                          <i class="uil uil-file-share-alt fs-3"></i>
                        </a>
                        <router-link
                          v-if="hasPermissionTo('U')"
                          class="btn btn-icon btn-sm btn-primary btn-hover"
                          :to="{
                            name: 'OrdersCreator',
                            params: {
                              action: 'edit',
                              reference: order.reference,
                            },
                          }"
                          ><i class="demo-pli-add fs-5"></i
                        ></router-link>
                        <a
                          v-if="hasPermissionTo('R') || hasPermissionTo('U')"
                          class="btn btn-icon btn-sm btn-success btn-hover"
                          href="javascript:void(0)"
                          @click.prevent="handleOrderPrint(order, i)"
                          ><i class="demo-pli-printer fs-5"></i
                        ></a>
                      </template>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div
              class="border-top border-bottom py-2 d-flex align-items-center flex-nowrap"
            >
              <div class="grant-total">
                <h5 class="mb-0">
                  Grand Total:
                  <span class="text-info">{{ formatMoney(grandTotal) }}</span>
                </h5>
              </div>
              <div class="paid-total mx-auto">
                <h5 class="mb-0">
                  Paid Total:
                  <span class="text-success">{{ formatMoney(paidTotal) }}</span>
                </h5>
              </div>
              <div class="paid-total ms-auto">
                <h5 class="mb-0">
                  Remain Total:
                  <span class="text-danger">{{
                    formatMoney(grandTotal - paidTotal)
                  }}</span>
                </h5>
              </div>
            </div>
            <pagination
              :from="recordsFrom"
              :to="recordsTo"
              :current_page="currentPage"
              :row_count_page="rowCountPage"
              @page-update="handlePagination"
              :total_records="totalRows"
              :page_range="pageRange"
            ></pagination>
          </div>
        </div>
      </div>
    </div>
  </div>

  <OrderModal
    :order="selectedOrder"
    v-if="!$helper.empty(selectedOrder)"
    @closed="closeSidebar"
    @paid="handlePaidOrder"
    @item-deleted="removeOrderItem"
    @request-split="handleSplitEvent"
  />

  <OrderReceipts
    :order="splittedOrder"
    v-if="!$helper.empty(splittedOrder) && splitingMode"
    @closed="closeSplitingOrder"
    @splitted="handleSplittedOrder"
  />
</template>
