                    name="Phone Number"
                  />
                </div>
              </div>

              <div class="row align-items-center mb-2">
                <div class="col-lg-3">
                  <label for="">Momo</label>
                </div>
                <div class="col-lg-9">
                  <input
                    type="text"
                    class="form-control no-left-radius"
                    v-model="newSettings.momo_code"
                    placeholder="Momo Pay code"
                    name="Momo"
                  />
                </div>
              </div>

              <div class="row align-items-center mb-2">
                <div class="col-lg-3">
                  <label for="">Address Line</label>
                </div>
                <div class="col-lg-9">
                  <input
                    type="text"
                    class="form-control no-left-radius"
                    v-model="newSettings.site_address"
                    placeholder="KN 145 ST 76, Nyarugenge, Kigali, Rwanda"
                    name="Address"
                  />
                </div>
