                </div>
                <div class="col-lg-9">
                  <input-file
                    :label="'Logo(<em>250px x 64x</em>)'"
                    :current-file="newSettings.site_logo"
                    @uploaded="handleUploadedFile($event, 'site_logo')"
                  ></input-file>
                </div>
              </div>

              <div class="row align-items-center mb-2">
                <div class="col-lg-3">
                  <label for="">Currency</label>
                </div>
                <div class="col-lg-9">
                  <select
                    class="form-control no-left-radius"
                    v-model="newSettings.currency"
                    name="Currency"
                  >
                    <option value="undefined" disabled hidden>
                      Select Currency
                    </option>
                    <option value="RWF">RWF</option>
                    <option value="$">USD</option>
                  </select>
                </div>
              </div>
              <div class="row align-items-center mb-2">
                <div class="col-lg-3"></div>
                <div class="col-lg-9">
                  <wolf-button
                    class="btn btn-primary"
                    @clicked="handleSubmit"
                    :disabler="submitDisabled"
                    activator="saving_records"
                  >
                    Save Changes
                  </wolf-button>
                </div>
              </div>
            </div>
