</template>
<script>
import InputFile from "./InputFile.vue";
import ContentHeader from "@/index/shared/ContentHeader.vue";
export default {
  components: { InputFile, ContentHeader },
  name: "GeneralSettings",
  data: () => ({
    imageKey: null,
    newSettings: {},
  }),
  computed: {
    submitDisabled() {
      return !this.newSettings.currency;
    },
  },
  mounted() {
    this.$http
      .post(
        "frontend/preloaders",
        this.$helper.generateFormData({
          keys: Object.keys(this.appSettings),
        })
      )
      .then((response) => {
        const result = response.data.result;
        this.newSettings = { ...this.newSettings, ...result };
      });
  },
  methods: {
    handleUploadedFile(filePath, key) {
      this.newSettings[key] = filePath;
    },
    handleSubmit() {
      const items = [];
      let obj = this.newSettings;
      for (let key in obj) {
        if (obj[key] !== null) {
          if (typeof obj[key] === "object")
            items.push({ key, value: JSON.stringify(obj[key]) });
          else items.push({ key, value: obj[key] });
        }
      }
      this.$http.post(
        "preloaders/update",
        this.$helper.generateFormData({ items })
      );
    },
  },
};
</script>
