<script>
import ContentHeader from "@/index/shared/ContentHeader.vue";
import EmptyResults from "@/components/EmptyResults.vue";
import Avatar from "@/components/Avatar.vue";
import OrderModal from "@/components/OrderModal.vue";
import ReceiptOne from "@/index/shared/ReceiptOne.vue";
import OrderReceipts from "@/components/OrderReceipts.vue";
import SplittedReceipt from "@/index/shared/SplittedReceipt.vue";

export default {
  name: "Orders",
  components: {
    ContentHeader,
    EmptyResults,
    Avatar,
    OrderModal,
    ReceiptOne,
    OrderReceipts,
    SplittedReceipt,
  },
  data: () => ({
    fetchUrl: "orders",
    selectedOrder: null,
    paymentMethod: "Cash",
    listingCategory: "all",
    rows: [],
    latestOrderInterval: null,
    newFilter: {
      status: "all",
      period: "all",
    },
    newDay: {
      from: new Date(TODAY)
        .toLocaleString("fr-CA", { timeZone: TIMEZONE })
        .slice(0, 10),
      to: new Date(TODAY)
        .toLocaleString("fr-CA", { timeZone: TIMEZONE })
        .slice(0, 10),
    },
    filters: {
      types: [
        {
          label: "All",
          value: "all",
        },
        {
          label: "On-Progress",
          value: "PENDING",
        },
        {
          label: "Completed",
          value: "DELIVERED",
        },
      ],
      periods: [
        {
          label: "All",
          value: "All",
        },
        {
          label: "Today",
          value: "Today",
        },
        {
          label: "Yesterday",
          value: "Yesterday",
        },
        {
          label: "This Week",
          value: "This Week",
        },
        {
          label: "This Month",
          value: "This Month",
        },
        {
          label: "This Year",
          value: "This Year",
        },
      ],
    },
    waiters: [],
    waitersFilterKey: null,
    searchKey: null,
    clientKey: null,
    client: null,
    selectedWaiter: null,
    printedOrder: {},
    splitingMode: false,
    splittedOrder: null,
  }),
  computed: {
    isEndOfDay() {
      return this.$route.name == "EodCreator";
    },
    filteredRows() {
      let status = this.newFilter.status != "all" && this.newFilter.status;
      let rows = this.rows;
      if (status) {
        rows = rows.filter((row) => row.status == status);
      }
      if (this.selectedWaiter) {
        rows = rows.filter((row) => row.waiter_id == this.selectedWaiter.id);
      }
      return rows;
    },
    filteredWaiters() {
      const keyword =
        this.waitersFilterKey && this.waitersFilterKey.toLowerCase();
      let waiters = this.waiters;
      if (keyword) {
        waiters = waiters.filter(
          (item) => String(item.name).toLowerCase().indexOf(keyword) > -1
        );
      }
      return waiters;
    },
    hasKeyword() {
      return this.searchKey != null && this.searchKey.trim().length >= 2;
    },
  },
  created() {
    this.resetFilters();
    this.$http.get(this.fetchUrl).then((response) => {
      this.rows = response.data.rows;
      if (!this.isEndOfDay) {
        this.fetchUrl = `orders?latest=${response.data.latest}`;
        this.getLatestOrder();
      } else {
        if (!this.rows.length) {
          this.createNewDay();
        }
      }
    });
  },
  methods: {
    handleSplitEvent() {
      this.openSplitModal(this.selectedOrder);
      this.closeSidebar();
    },
    openSplitModal(order) {
      this.splittedOrder = { ...order };
      this.splitingMode = true;
    },
    closeSplitingOrder() {
      this.splittedOrder = null;
      this.splitingMode = false;
    },
    handleSplittedOrder(receipts) {
      const index = this.rows.findIndex(
        (item) => item.id == this.splittedOrder.id
      );
      this.rows[index].receipts = receipts;
      this.closeSplitingOrder();
    },
    handlePrintedOrder() {
      const index = this.rows.findIndex(
        (item) => item.id == this.selectedOrder.id
      );
      this.rows[index].status = "DELIVERED";
      this.rows[index].printed = 1;
      //this.rows[index].resolved = 1;
      this.selectedOrder = this.rows[index];
      this.printedOrder = this.rows[index];
      if (
        !Array.isArray(this.printedOrder.receipts) &&
        !this.printedOrder?.receipts?.length
      ) {
        setTimeout(() => this.$helper.handlePrint("#print-container"), 2000);
      }
      //setTimeout(() => this.$helper.handlePrint("#print-container"), 1000);
    },
    createNewDay() {
      this.$http
        .post("settings/close-day", this.$helper.generateFormData(this.newDay))
        .then((response) => {
          if (response.data.status) {
            this.$store.state.isCorrectSystemDay = true;
            this.$store.commit("SET_FLASH_MESSAGE", {
              type: "success",
              message: "END OF DAY created successfully",
            });
            //window.localStorage.removeItem("__last_printed_round");
            this.$router.push({ name: "Dashboard" });
          }
        });
    },
    handlePaidOrder() {
      const index = this.rows.findIndex(
        (item) => item.id == this.selectedOrder.id
      );
      this.rows.splice(index, 1);
      this.closeSidebar();
    },
    handleWaiterFilter(waiter) {
      this.selectedWaiter = waiter;
    },
    resetFilters() {
      this.newFilter.status = this.filters.types[0].value;
      this.newFilter.period = this.filters.periods[0].value;
    },
    getLatestOrder() {
      this.latestOrderInterval = setInterval(() => {
        this.$store.commit("SET_REQUEST_FLAG", "GETTING_LATEST_ORDERS");
        this.$http.get(this.fetchUrl).then((response) => {
          if (response.data.rows.length) {
            this.rows = [...response.data.rows, ...this.rows];
            this.fetchUrl = `orders?latest=${response.data.latest}`;
          }
        });
      }, 3000);
    },
    selectOrder(order) {
      if (order) {
        document.querySelector("#root").classList.add("sb--show");
      } else {
        document.querySelector("#root").classList.remove("sb--show");
      }
      this.selectedOrder = order;
    },
    closeSidebar() {
      document.querySelector("#root").classList.remove("sb--show");
      this.selectedOrder = null;
    },
    handleSearch(fetchUrl = null) {
      let url = fetchUrl;
      if (this.hasKeyword) {
        this.$store.commit("SET_REQUEST_FLAG", "PAGINATION_SEARCH");
        if (url.indexOf("?") > -1)
          url += `&q=${this.filterKey}&per_page=${this.rowCountPage}`;
        else url += `?q=${this.filterKey}&per_page=${this.rowCountPage}`;
      }
      this.$http.get(url).then((response) => {
        this.rows = response.data.rows;
      });
    },
    handleFilter() {
      let fetchUrl = this.$helper.encodeQuery("orders", this.newFilter);
      this.$store.commit("SET_REQUEST_FLAG", "PAGINATION_SEARCH");
      this.handleSearch(fetchUrl);
    },
  },
  beforeUnmount() {
    clearInterval(this.latestOrderInterval);
  },
  mounted() {
    this.$store.commit("SET_REQUEST_FLAG", "GET_FILTERS_RESOURCES");
    this.$http.get("users").then((response) => {
      this.waiters = response.data.users;
    });
    this.getPaymentsMeta();
    /*this.$http.all([this.$http.get("users")]).then(
      this.$http.spread((responseTwo) => {
        
      })
    ); */
    this.$nextTick(() => {
      if (this.isEndOfDay) {
        const start_date = this.$route.query?.start_date;
        const end_date = this.$route.query?.end_date;
        this.newDay.from = new Date(start_date)
          .toLocaleString("fr-CA", { timeZone: TIMEZONE })
          .slice(0, 10);
        this.newDay.from = new Date(end_date)
          .toLocaleString("fr-CA", { timeZone: TIMEZONE })
          .slice(0, 10);
      }
    });
  },
};
</script>
<template>
  <content-header
    v-if="isEndOfDay"
    title="Unresolved Orders List"
    subtitle="Delete or assigned the following orders to start a new day"
    classes="custom"
  ></content-header>
  <content-header
    v-else
    title="Orders List"
    subtitle="View All orders that are in progress or completed"
    classes="custom"
  ></content-header>
  <div class="container-fluid mt-2">
    <div class="row">
      <div class="pt-1 col-lg-12">
        <div class="d-flex align-items-center">
          <div class="d-flex align-items-center flex-nowrap">
            <span class="h6 mb-0 me-1">Filter By:</span>
            <div class="btn-group">
              <button
                type="button"
                class="btn btn-light border bg-white rounded-1 dropdown-toggle"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                Type: <span class="fw-bolder">{{ newFilter.status }}</span>
              </button>
              <ul class="dropdown-menu data-filters" style="">
                <li v-for="(item, i) in filters.types" :key="'order-types' + i">
                  <a
                    class="dropdown-item"
                    @click.prevent="newFilter.status = item.value"
                    :class="{ active: newFilter.status == item.value }"
                    href="javascript:void(0)"
                    >{{ item.label }}</a
                  >
                </li>
              </ul>
            </div>
            <div class="btn-group">
              <button
                type="button"
                class="btn btn-light border bg-white ms-1 rounded-1 dropdown-toggle"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                Period : <span class="fw-bolder">{{ newFilter.period }}</span>
              </button>
              <div class="dropdown-menu">
                <div :class="{ 'd-flex': newFilter.period == 'custom' }">
                  <ul class="ranges data-filters" style="">
                    <li
                      v-for="(item, i) in filters.periods"
                      :key="'periods' + i"
                    >
                      <a
                        class="dropdown-item"
                        @click.prevent="newFilter.period = item.value"
                        :class="{ active: newFilter.period == item.value }"
                        href="javascript:void(0)"
                        >{{ item.label }}</a
                      >
                    </li>
                    <li>
                      <a
                        class="dropdown-item"
                        href="javascript:void(0)"
                        @click.prevent="newFilter.period = 'custom'"
                        :class="{ active: newFilter.period == 'custom' }"
                        >Custom</a
                      >
                    </li>
                  </ul>
                  <div
                    class="custom-range border-start d-block ps-2"
                    v-if="newFilter.period == 'custom'"
                  >
                    <div class="custom-ranger-wraper">
                      <div class="date-picker">
                        <div class="form-group mb-2">
                          <label for="">From:</label>
                          <input
                            type="date"
                            name=""
                            id=""
                            class="form-control"
                          />
                        </div>
                        <div class="form-group">
                          <label for="">To:</label>
                          <input
                            type="date"
                            name=""
                            id=""
                            class="form-control"
                          />
                        </div>
                      </div>
                      <div class="bottom-0 text-end">
                        <button class="btn btn-sm btn-primary" type="button">
                          Apply
                        </button>
                        <button class="btn btn-sm btn-light ms-1" type="button">
                          Cancel
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <button
              class="btn btn-icon btn-light bg-white"
              type="button"
              @click="reloadPage"
            >
              <svg
                height="18px"
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15"
                ></path>
              </svg>
            </button>
          </div>
          <div class="ms-auto">
            <div class="d-flex flex-nowrap align-items-center">
              <div class="form-group">
                <input
                  type="text"
                  placeholder="Search by Order ID..."
                  class="form-control"
                  autocomplete="off"
                />
              </div>
              <!-- Start of Waiters Filter -->
              <div class="icon">
                <div class="dropdown">
                  <a
                    href="javascript:void(0)"
                    data-bs-toggle="dropdown"
                    class="btn btn-icon btn-light bg-white"
                  >
                    <img
                      :src="`${publicPath}img/waiter.png`"
                      alt="waiter"
                      style="width: 18px"
                    />
                  </a>
                  <div
                    class="dropdown-menu dropdown-menu-end w-md-200px w-sm-250px"
                    data-popper-placement="bottom-end"
                  >
                    <div class="border-bottom px-1 py-2">
                      <div class="form-group">
                        <input
                          type="search"
                          placeholder="Search for a waiter"
                          class="form-control"
                          autocomplete="off"
                          v-model="waitersFilterKey"
                        />
                      </div>
                    </div>

                    <div
                      class="list-group list-group-borderless listgroup-search scrollable-content"
                    >
                      <div
                        v-for="waiter in filteredWaiters"
                        :key="waiter.id"
                        class="list-group-item list-group-item-action d-flex align-items-start mb-2"
                        :class="{
                          'selected-option':
                            selectedWaiter && selectedWaiter.id == waiter.id,
                        }"
                        @click="handleWaiterFilter(waiter)"
                      >
                        <div class="flex-shrink-0 me-2">
                          <avatar
                            :name="waiter.name"
                            width="28px"
                            height="28px"
                          ></avatar>
                        </div>
                        <div class="flex-grow-1">
                          <a
                            href="javascript:void(0)"
                            class="h6 d-block mb-0 stretched-link text-decoration-none"
                            >{{ waiter.name }}</a
                          >
                          <small class="text-muted">
                            @{{
                              waiter.name.toLowerCase().split(" ").join("_")
                            }}</small
                          >
                        </div>
                      </div>
                      <span
                        class="py-2 text-center"
                        v-if="!filteredWaiters.length"
                        >No waiters found</span
                      >
                    </div>
                  </div>
                </div>
              </div>
              <!-- END --->
            </div>
          </div>
        </div>
        <div
          class="d-flex justify-content-center"
          style="min-height: 65vh"
          v-if="!filteredRows.length"
        >
          <empty-results>
            <template #description
              ><span
                >No orders found. Please wait for new orders</span
              ></template
            >
          </empty-results>
        </div>
        <div class="main-side scrollable-content w-100 pt-3" v-else>
          <div class="order-wraper d-grid">
            <a
              href="javascript:void(0)"
              class="rounded-2 box-shadow bg-white border p-2 order-list-item mb-2 position-relative"
              v-for="(order, i) in filteredRows"
              :key="'order' + i"
              @click.prevent="selectOrder(order)"
              :class="{ active: selectedOrder && selectedOrder.id == order.id }"
            >
              <span
                class="badge bg-success rounded-circle completed-order p-1"
                v-if="order.printed == 1"
              >
                <i class="demo-pli-yes fs-5"></i>
              </span>
              <div class="d-flex align-items-center flex-nowrap">
                <div class="order-left">
                  <h5 class="mb-0 fw-bolder">
                    Order: #{{ $helper.generateVoucherNo(order.id) }}
                  </h5>
                  <a href="javascript:void(0)" class="m-0 h6">{{
                    order.table.name
                  }}</a>
                  <h6 class="m-0 text-muted">
                    By:
                    {{ order.waiter ? order.waiter.name : "Administrator" }}
                  </h6>
                  <span class="text-primary">Qta {{ order.items.length }}</span>
                </div>
                <div class="ms-auto text-end">
                  <p class="m-0 text-dark">
                    {{ $helper.formatOrderDate(order.created_at) }}
                  </p>
                  <p class="mb-0 text-muted">
                    {{ $helper.formatOrderTime(order.created_at) }}
                  </p>
                  <div class="d-flex flex-nowrap">
                    <h5 class="m-0">
                      <span class="fw-bolder text-dark">{{
                        formatMoney(order.grand_total)
                      }}</span>
                      <span class="badge bg-secondary ms-2">{{
                        order.category
                      }}</span>
                    </h5>
                  </div>
                </div>
              </div>
            </a>
          </div>
        </div>
      </div>
    </div>
  </div>
  <OrderModal
    :order="selectedOrder"
    v-if="!$helper.empty(selectedOrder)"
    @closed="closeSidebar"
    @paid="handlePaidOrder"
    @deleted="handlePaidOrder"
    @printed="handlePrintedOrder"
    @request-split="handleSplitEvent"
  />
  <div
    class="w-100 fixed-bottom shadow border-top p-2 main-content bg-white"
    style="z-index: 999; left: 11rem"
    v-if="isEndOfDay"
  >
    <div class="d-flex w-100 align-items-center">
      <div class="form-group" style="width: 200px">
        <label for="from-date">System Date</label>
        <input
          type="date"
          class="form-control form-control-lg"
          v-model="newDay.from"
          id="from-date"
        />
      </div>
      <div class="form-group mx-2" style="width: 200px">
        <label for="to-date">Current Date</label>
        <input
          type="date"
          class="form-control form-control-lg"
          v-model="newDay.to"
          id="to-date"
        />
      </div>
      <div class="form-group">
        <label for="from-date" class="invisible d-block">Confirm</label>
        <wolf-button
          type="button"
          @clicked="createNewDay"
          :disabler="rows.length"
          activator="CREATING_EDO"
          class="btn btn-primary"
          >Confirm</wolf-button
        >
      </div>
    </div>
  </div>

  <div class="d-none">
    <receipt-one :order="printedOrder"></receipt-one>
  </div>

  <template v-if="printedOrder.receipts">
    <div
      class="d-none"
      v-for="receipt in printedOrder.receipts"
      :key="'receipt' + printedOrder.id"
    >
      <SplittedReceipt
        :order="{
          ...printedOrder,
          ...{ grand_total: receipt.grand_total, items: receipt.items },
        }"
      />
    </div>
  </template>

  <OrderReceipts
    :order="splittedOrder"
    v-if="!$helper.empty(splittedOrder) && splitingMode"
    @closed="closeSplitingOrder"
    @splitted="handleSplittedOrder"
  />
</template>
